.name-data {
  margin: 0 20px 16px 20px;
  padding: 0;
  border: none;

  .name-data__list {
    @include reset();
  }
}

.name-data__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 27px;
  line-height: 30px;

  label {
    margin-left: 23px;
    margin-bottom: 15px;
    color: #d22856;
    text-transform: uppercase;
  }

  input {
    padding: 22px;
    font-size: 18px;
    line-height: 30px;
  }
}

@media (min-width: $tablet-width) {
  .name-data {
    order: 1;
  }

  .name-data__item {
    display: flex;
    flex-direction: row;
    align-items: center;

    label {
      width: 30%;
      margin-bottom: 0;
    }

    input {
      flex-grow: 1;
    }
  }
}

@media (min-width: $desktop-width) {
  .name-data {
    width: 48%;
    margin: 0 0 16px 0;
  }
}
